@import url(http://fonts.cdnfonts.com/css/lt-wave);
:root {
  --error-color: rgba(223, 95, 78, 0.966);
  --border-img: rgb(206, 200, 200);
  --first-color: #d0d5d881;
  --second-color: rgba(42, 44, 42, 0.507);
  --contrast-color: #0c8d13;
  --gradient: linear-gradient(139deg, #2e7c33ef 0%, #2fb3df 100%);
  --bgc-info: #f3f1f16e;
  --black-color: rgb(15, 14, 14);
  --white-color: rgb(237, 238, 237);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  font-size: 10px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(139deg, #2e7c33ef 0%, #2fb3df 100%);
  background-image: var(--gradient);
}

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
}

.firstColor {
  background-color: #d0d5d881;
  background-color: var(--first-color);
  color: rgb(15, 14, 14);
  color: var(--black-color);
}
.secondColor {
  background-color: rgba(42, 44, 42, 0.507);
  background-color: var(--second-color);
  color: rgb(237, 238, 237);
  color: var(--white-color);
}


.app {
	width: 1400px;
    height: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card {
	position: relative;
	width: 1200px;
	height: 800px;
	border-radius: 15px;
	text-align: center;
	box-shadow: 5px 0px 33px -1px rgba(107, 103, 103, 0.75);
}

.card__title {
	text-transform: uppercase;
	padding: 20px;
    font-size: 2rem;
	border-top-right-radius: 15px;
	border-top-left-radius: 15px;
	box-shadow: 5px 0px 33px -1px rgba(0,0,0,0.75);
}

.card__btnColor {
	position: absolute;
	top: 0;
	right: 0;
	padding: 25px;
	background-color: transparent;
	border: none;
	cursor: pointer;
	transition: -webkit-transform 0.2s ease-in;
	transition: transform 0.2s ease-in;
	transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
}

.card__btnColor:hover {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}
.card__btnColor:active {
	-webkit-transform: scale(0.8) rotate(180deg);
	        transform: scale(0.8) rotate(180deg);
}

.card__boxes {
	display: flex;
	justify-content: space-around;
	margin-top: 40px;
}

.calculator {
	width: 47%;
	height: 650px;
	border-radius: 15px;
}

.calculator__items {
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-around;
    padding: 30px 60px;
    text-transform: uppercase;
}

.calculator__items label {
    display: inline-block;
    width: 170px;
    font-size: 1.1rem;
    text-align: left;
    margin-right: 50px;
}

.calculator__items input {
    width: 200px;
    height: 30px;
    padding: 5px;
}

.calculator__items select {
    width: 200px;
    height: 30px;
    padding: 5px;
}

.errorInfo {
    margin-top: 5px;
    text-align: right;
    padding: 2px;
    font-size: 1.13rem;
    color: var(--error-color);
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: none;
    font-family: 'LT Wave', sans-serif;
}



.calculator__price {
	margin: 30px auto;
	padding: 10px 5px;
	width: 390px;
	height: 50px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.calculator__price__box {
	display: flex;
	justify-content: space-around;
	align-items: center;
	text-transform: uppercase;
	font-size: 1.8rem;
	border-bottom: 2px solid var(--contrast-color);
}

.calculator__price__title {
	margin-right: 50px;
}

.calculator__calculateBtn {
	position: relative;
	right: 35px;
	text-transform: uppercase;
	font-weight: bold;
	font-family: 'Squad', sans-serif;
	letter-spacing: .5px;
	font-size: 1.8rem;
	padding: 6px 20px;
	border-radius: 15px;
	cursor: pointer;
	border: 2px solid var(--contrast-color);
	transition: -webkit-transform 0.3s ease-in;
	transition: transform 0.3s ease-in;
	transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
}

.calculator__calculateBtn:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.calculator__calculateBtn:active {
	-webkit-transform: scale(0.8);
	        transform: scale(0.8);
}

.informations {
    width: 47%;
    height: 650px;
    background-color: var(--bgc-info);
    border-radius: 15px;
}

.informations__imgs {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 50%;
    
}

.informations__imgs--img {
    width: 160px;
    height: 160px;
    border: 1px solid var(--border-img);
    object-fit: cover;
    cursor: pointer;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
}

.informations__imgs--img:hover{
    -webkit-transform: scale(2.2);
            transform: scale(2.2);
}

.informations__description {
    width: 100%;
    max-height: 50%;
    margin-top: 20px;
    text-align: left;
    padding: 20px;
    overflow: auto;
    color:var(--black-color);
}

.informations__description__title {
    font-size: 2.2rem;
    margin: 15px 0;
}

.informations__description__text {
    font-size: 1.5rem;
}
